<template>
  <div class="outerBox" ref="msgWindow">
    <div class="container">
      <div class="d-flex titleMsg">
        <figure>
          <img v-show="hatoUrl1" src="../assets/hato1.png" alt="" />
          <img v-show="!hatoUrl1" src="../assets/hato2.png" alt="" />
        </figure>
        <div class="mt-md-5">
          <p class="mt-3 mb-1">我是智能客服小鴿子，</p>
          <p class="my-1">如果有任何問題，</p>
          <p class="my-1">請打在下面跟我說唷～</p>
        </div>
      </div>
      <div class="msgInput">
        <input @keydown.enter="submitMsg" v-model.trim="msg" class="form-control" type="text" />
        <i @click="submitMsg" class="bi bi-send h2 ps-2 mb-0 mt-1"></i>
      </div>
      <ul>
        <li v-for="(liMsg, index) in msgBox" :class="index % 2 ? 'msgFromCs' : 'msgFromUser'" :key="index">
          <span>{{ liMsg }}</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      hatoUrl1: true,
      msg: '',
      msgBox: []
    }
  },
  methods: {
    submitMsg() {
      if (this.msg) {
        this.msgBox.push(this.msg)
        this.msgBox.push('不告訴你欸嘿>wO')

        this.msg = ''
        this.$nextTick(() => {
          this.$refs.msgWindow.scrollTop = this.$refs.msgWindow.scrollHeight
        })
      }
    }
  },
  mounted() {
    setInterval(() => {
      this.hatoUrl1 = !this.hatoUrl1
    }, 500)
  }
}
</script>

<style lang="less" scoped>
.outerBox {
  overflow-y: auto;
  .container {
    height: 70vh;
    img {
      width: 100%;
      height: 100%;
    }
    .titleMsg {
      font-size: 1.8rem;
      @media (max-width: 767px) {
        justify-content: space-between;
        padding-right: 2rem;
        font-size: 1rem;
      }
      @media (max-width: 300px) {
        padding-right: 0;
      }
      figure {
        width: 30vw;
      }
    }
    .msgInput {
      display: flex;
      position: fixed;
      bottom: 1rem;
      @media (max-width: 767px) {
        bottom: var(--phoneNavHeight);
      }
      left: 0;
      padding: 1rem;
      width: 100%;
    }
    ul {
      span {
        margin: 0.75rem 0;
        padding: 1px 0.5rem;
        border-radius: 0.25rem;
      }
      .msgFromUser {
        overflow: hidden;
        span {
          float: right;
          background-color: rgb(126, 237, 126);
        }
      }
      .msgFromCs span {
        background-color: rgb(215, 214, 214);
      }
    }
  }
}
</style>
