<template>
  <div class="container col-md-6">
    <form class="mt-5 row g-3 needs-validation" novalidate>
      <div id="accountChecker" class="d-none">
        <p class="alert alert-success m-0" role="alert" v-if="success">註冊成功！<router-link to="/login">點擊返回登入頁面</router-link></p>
        <p class="alert alert-danger m-0" role="alert" v-if="!success">帳號已遭使用，請使用其他帳號重新註冊。</p>
      </div>
      <div>
        <label for="validationCustom03" class="form-label">帳號</label>
        <input v-model.trim="account" type="text" class="form-control" required />
        <div class="invalid-feedback">尚未輸入帳號。</div>
      </div>
      <div>
        <label for="validationCustom03" class="form-label">密碼</label>
        <input v-model.trim="password" type="password" class="form-control" required />
        <div class="invalid-feedback">尚未輸入密碼。</div>
      </div>
      <div>
        <label for="validationCustom03" class="form-label">暱稱</label>
        <input v-model.trim="userName" type="text" class="form-control" required />
        <div class="invalid-feedback">尚未輸入帳號。</div>
      </div>
      <div class="col-12 d-flex justify-content-between">
        <button class="btn btn-primary" type="submit">註冊</button>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      account: '',
      password: '',
      userName: '',
      success: false
    }
  },
  mounted() {
    let vm = this
    ;(function () {
      'use strict'

      // Fetch all the forms we want to apply custom Bootstrap validation styles to
      var forms = document.querySelectorAll('.needs-validation')

      // Loop over them and prevent submission
      Array.prototype.slice.call(forms).forEach(function (form) {
        form.addEventListener(
          'submit',
          async function (event) {
            event.preventDefault()
            if (!form.checkValidity()) {
              event.stopPropagation()
            } else {
              // php的$_POST['']只認得FormData()實例對象格式的參數
              let data = new FormData()
              data.append('account', vm.account)
              data.append('password', vm.password)
              data.append('userName', vm.userName)

              // TODO: 打包後在dist資料夾內複製一份doSignup.php
              let { data: result } = await vm.$axios.post('./doSignup.php', data)
              if (result.status == 1) {
                vm.success = true
              } else {
                vm.success = false
              }
              document.querySelector('#accountChecker').classList.remove('d-none')
            }

            form.classList.add('was-validated')
          },
          false
        )
      })
    })()
  }
}
</script>

<style lang="less" scoped></style>
