<template>
  <div class="hacoinBox">
    <h2>鴿幣：{{ user.hacoin }}<span class="bi bi-coin ms-2"></span></h2>
  </div>
  <div class="container">
    <ul class="d-flex justify-content-around">
      <li :class="liActive == 1 ? 'active' : ''" @click="liActive = 1">會員資料</li>
      <!-- 我的課程需串接資料庫 -->
      <li :class="liActive == 2 ? 'active' : ''" @click="liActive = 2">尚未開放</li>
    </ul>
    <div class="w-75 mx-auto mt-4">
      <table v-show="liActive == 1" class="table h5">
        <tr>
          <td>暱稱</td>
          <td>{{ user.userName }}</td>
        </tr>
        <tr>
          <td>帳號</td>
          <td>{{ user.account }}</td>
        </tr>
      </table>

      <p v-show="liActive == 2" class="h5">串接資料庫後預計新增「我的課程」功能，敬請期待。</p>
    </div>
    <button @click="logout" class="d-block mt-5 btn btn-secondary mx-auto w-25">登出</button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user: {},
      liActive: 1
    }
  },
  methods: {
    logout() {
      localStorage.removeItem('token')
      this.$router.go(0)
    }
  },
  created() {
    this.user = JSON.parse(localStorage.getItem('token'))
  }
}
</script>

<style lang="less" scoped>
.hacoinBox {
  position: relative;
  margin: 10vh auto 5vh;
  width: 40rem;
  height: 20rem;
  @media (max-width: 767px) {
    width: 80vw;
    height: 25vh;
  }
  border-radius: 1rem;
  overflow: hidden;
  background: url('../assets/creditCard.png') no-repeat center center;
  background-size: 100%;
  h2 {
    position: absolute;
    left: 13rem;
    bottom: 11rem;
    @media (max-width: 767px) {
      left: 1.8rem;
      bottom: 2rem;
    }
    margin: 0;
    color: white;
  }
}
.container {
  @media (min-width: 768px) {
    width: 50%;
  }
  ul {
    li {
      padding: 1rem 2rem;
      @media (max-width: 767px) {
        padding: 0.5rem 1rem;
      }
    }
    li:hover {
      background-color: rgb(239, 239, 239);
    }
    li.active {
      border-bottom: 0.3rem solid rgb(255, 112, 112);
    }
  }
  table {
    tr {
      td {
        line-height: 3rem;
      }
    }
  }
}
</style>
