<template>
  <div>
    <Header @logout="logout" :loginStateFromFather="loginState"></Header>
    <div class="mt">
      <router-view @loginSuccess="loginSuccess"></router-view>
    </div>
    <PhoneNav @logout="logout" :loginStateFromFather="loginState"></PhoneNav>
  </div>
</template>

<script>
import Header from './components/Hato-header.vue'
import PhoneNav from './components/Hato-phoneNav.vue'
export default {
  components: {
    Header,
    PhoneNav
  },
  data() {
    return {
      loginState: false,
      hacoin: 0,
      userState: {}
    }
  },
  methods: {
    loginSuccess(state) {
      this.loginState = true
      this.userState = state
    },
    logout() {
      this.loginState = false
    }
  },
  created() {
    if (localStorage.getItem('token')) {
      this.loginState = true
    }
  }
}
</script>

<style lang="less" scoped>
.mt {
  margin-top: var(--headerHeight);
}
</style>
