<template>
  <div class="container">
    <h1 class="mb-md-3">Ha老師的精華</h1>
    <div class="d-md-flex justify-content-between">
      <p class="order-2">鴿幣：{{ user.hacoin }}</p>
      <ul class="d-flex order-1">
        <li class="me-3"><a @click="filterMode = 'all'" :class="`tab ${filterMode == 'all' ? 'active' : ''}`" href="javascript:;">全部</a></li>
        <li class="me-3"><a @click="filterMode = 'favorite'" :class="`tab ${filterMode == 'favorite' ? 'active' : ''}`" href="javascript:;">最愛</a></li>
        <li class="me-3"><a @click="filterMode = 'hot'" :class="`tab ${filterMode == 'hot' ? 'active' : ''}`" href="javascript:;">熱門</a></li>
        <li @mouseover="$refs.dropMenu.style.height = '86.4px'" @mouseleave="$refs.dropMenu.style.height = '0px'" class="me-3">
          <a :class="`tab ${filterMode == 'kind-vue' || filterMode == 'kind-js' ? 'active' : ''}`" href="javascript:;">類型▾</a>
          <ul class="mt-2 dropMenu" ref="dropMenu">
            <li><a :class="`subtab ${filterMode == 'kind-vue' ? 'active' : ''}`" @click="filterMode = 'kind-vue'" class="d-block" href="javascript:;">vue</a></li>
            <li><a :class="`subtab ${filterMode == 'kind-js' ? 'active' : ''}`" @click="filterMode = 'kind-js'" class="d-block" href="javascript:;">js</a></li>
          </ul>
        </li>
      </ul>
    </div>

    <ul class="row p-3">
      <li class="card bg-light col-md-3 mb-3 m-md-5 p-4 flexCol position-relative" v-for="product in showProductList" :key="product.id">
        <div>
          <img v-if="product.hot" src="../assets/hot.png" class="hot" />
          <img v-if="product.kind == 'vue'" src="../assets/hatoIsTeaching.png" alt="" />
          <img v-if="product.kind == 'js'" src="../assets/hatoIsTeachingJs.png" alt="" />
          <h2>{{ product.name }}</h2>
          <p>價格：{{ product.price == 0 ? '限時免費！' : product.price }}</p>
          <p>{{ product.discribe }}</p>
        </div>
        <div>
          <div class="d-flex justify-content-between">
            <button @click="pay(product.id, product.price)" v-if="!product.bought" class="btn btn-primary">購買</button>
            <a :href="product.url" v-if="product.bought" class="btn btn-success" target="_blank">影片</a>
            <button :class="`btn ${product.favorite ? 'btn-secondary' : 'btn-primary'}`" @click="toggleCart(product.id)">{{ product.favorite ? '移除最愛' : '加到最愛' }}</button>
          </div>
        </div>
      </li>
    </ul>
    <p v-show="filterMode == 'favorite'">
      小計：<span>{{ subTotal }}</span>
    </p>
    <button v-show="filterMode == 'favorite'" class="btn btn-primary">全部購買（{{ showProductList.length }}）</button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      productList: [
        {
          id: 0,
          name: 'Vue2-3基礎——簡介到安裝',
          kind: 'vue',
          price: 99999,
          hot: false,
          discribe: '由於目前尚有企業使用vue2系統，本教程教授可同時運用於vue3及vue2的知識點，並且以vue-cli入門，以利同學快速銜接企業開發環境。',
          favorite: false,
          bought: false,
          url: ''
        },
        {
          id: 1,
          name: 'Vue2-3基礎——基本構成',
          kind: 'vue',
          price: 100,
          hot: true,
          discribe: '課程內容為data()、methods、computed、watch等vue的基礎構成，目標讓學員可以理解上述功能及使用情境。',
          favorite: false,
          bought: false,
          url: ''
        },
        {
          id: 2,
          name: 'Vue2-3基礎——邏輯運算',
          kind: 'vue',
          price: 100,
          hot: true,
          discribe: '課程內容為if、for等vue的基礎邏輯運算，目標讓學員可以理解上述功能及使用情境。',
          favorite: false,
          bought: false,
          url: ''
        },
        {
          id: 3,
          name: 'Vue2-3基礎——生命週期',
          kind: 'vue',
          price: 0,
          hot: true,
          discribe: '用極淺顯易懂的方式使學員快速理解生命週期及常用生命週期函數的使用方式。',
          favorite: false,
          bought: false,
          url: 'https://www.youtube.com/watch?v=bo4CFNMCwSs'
        },
        {
          id: 4,
          name: 'Vue2-3基礎——組件',
          kind: 'vue',
          price: 99999,
          hot: false,
          discribe: '組件切換、顯示與否等等。',
          favorite: false,
          bought: false,
          url: ''
        },
        {
          id: 5,
          name: 'Vue2-3基礎——路由',
          kind: 'vue',
          price: 99999,
          hot: false,
          discribe: '路由基本使用方式。',
          favorite: false,
          bought: false,
          url: ''
        },
        {
          id: 10,
          name: 'javascript基礎',
          kind: 'js',
          price: 99999,
          hot: false,
          discribe: '課程規劃中，敬請期待。',
          favorite: false,
          bought: false,
          url: ''
        },
        {
          id: 19,
          name: '以宗教玄學的觀點淺談原型鏈',
          kind: 'js',
          price: 99999,
          hot: false,
          discribe: '你有想過我們所處的世界其實是由造物者透過程式語言編寫出來的嗎？俗話說萬物皆對象，你我也不過是封裝了一些屬性及方法的數據。',
          favorite: false,
          bought: false,
          url: ''
        }
      ],
      filterMode: 'all',
      NoticeHeight: 0,
      user: {}
    }
  },
  methods: {
    toggleCart(i) {
      // this.productList[i].favorite = !this.productList[i].favorite
      this.productList.map((product) => {
        if (product.id == i) product.favorite = !product.favorite
      })
      let favoriteArr = []
      this.productList.forEach((product) => {
        if (product.favorite) {
          favoriteArr.push(product.id)
        }
      })
      localStorage.setItem('hatoLessonFavorite', favoriteArr.join(','))
    },
    pay(id, price) {
      if (!localStorage.getItem('token')) {
        alert('請先登入。')
        return this.$router.push('/login')
      }
      if (this.user.hacoin < price) {
        return alert('餘額不足。')
      }
      let name
      this.productList.map((product) => {
        if (product.id == id) {
          name = product.name
          product.bought = true
          this.user.hacoin -= price
          localStorage.setItem('token', JSON.stringify(this.user))
          return
        }
      })
      alert(`購買了「${name}」，\n花費${price}元。`)
    }
  },
  computed: {
    showProductList() {
      switch (this.filterMode) {
        case 'all':
          return this.productList
        case 'favorite':
          return this.productList.filter((product) => product.favorite)
        case 'hot':
          return this.productList.filter((product) => product.hot)
        case 'kind-vue':
          return this.productList.filter((product) => product.kind == 'vue')
        case 'kind-js':
          return this.productList.filter((product) => product.kind == 'js')
        default:
          return []
      }
    },
    subTotal() {
      let sum = 0
      this.showProductList.forEach((element) => {
        if (element.price != '限時免費！') {
          sum += Number(element.price)
        }
      })
      return sum
    }
  },
  created() {
    if (localStorage.getItem('hatoLessonFavorite')) {
      let favoriteArr = localStorage.getItem('hatoLessonFavorite').split(',')
      this.productList.forEach((product) => {
        if (favoriteArr.some((ids) => ids == product.id)) {
          product.favorite = true
        }
      })
    }
    if (localStorage.getItem('token')) {
      this.user = JSON.parse(localStorage.getItem('token'))
    }
  }
}
</script>

<style scoped>
.container {
  position: relative;
  padding-top: 15px;
  padding-bottom: 50px;
}
@media (max-width: 767px) {
  .container h1 {
    padding-left: 0.8rem;
  }
}
li > a {
  padding: 0.6rem 0.8rem;
  text-decoration: none;
}
a:hover {
  background-color: rgb(238, 238, 238);
}
/* a標籤 */
.tab.active {
  border-bottom: 0.3rem solid palevioletred;
}
.subtab.active {
  font-weight: 700;
}
.flexCol {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.dropMenu {
  overflow: hidden;
  height: 0;
  transition: 0.3s;
}
ul {
  margin-bottom: 0px;
}
.hot {
  position: absolute;
  right: -1rem;
  top: -0.7rem;
}
p span {
  text-decoration: underline;
}
</style>
