import { createRouter, createWebHashHistory } from 'vue-router'
import Products from '../views/Hato-products.vue'
import Login from '../views/Hato-login.vue'
import Cs from '../views/Hato-cs.vue'
import Signup from '../views/Hato-signup.vue'
import Center from '../views/Hato-center.vue'

const routes = [
  { path: '/products',component: Products},
  { path:'/',redirect:'/products'},
  {path:'/login',component:Login},
  {path:'/signup',component:Signup},
  {path:'/cs',component:Cs},
  {path:'/center',component:Center}
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

router.beforeEach((to,from,next)=>{
  if(to.path=='/center'&&!localStorage.getItem('token')){
    next('/login')
  }else{
    next()
  }
})

export default router
