<template>
  <div class="container col-md-6">
    <form class="mt-5 row g-3 needs-validation" novalidate>
      <div ref="accountChecker" class="d-none">
        <p class="alert alert-danger m-0" role="alert">帳號或密碼有誤。</p>
      </div>
      <div>
        <label for="validationCustom03" class="form-label">帳號</label>
        <!-- <input v-model.trim="account" type="text" class="form-control" id="validationCustom03" required /> -->
        <input v-model.trim="account" type="text" class="form-control" required />
        <div class="invalid-feedback">尚未輸入帳號。</div>
      </div>
      <div>
        <label for="validationCustom03" class="form-label">密碼</label>
        <!-- <input v-model.trim="password" type="password" class="form-control" id="validationCustom03" required /> -->
        <input v-model.trim="password" type="password" class="form-control" required />
        <div class="invalid-feedback">尚未輸入密碼。</div>
      </div>
      <div class="col-12 d-flex justify-content-between">
        <button class="btn btn-primary" type="submit">登入</button>
        <span class="pt-2">還沒有帳號嗎？<router-link to="/signup">點我註冊</router-link></span>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      account: '',
      password: ''
    }
  },
  mounted() {
    let vm = this
    ;(function () {
      'use strict'

      // Fetch all the forms we want to apply custom Bootstrap validation styles to
      var forms = document.querySelectorAll('.needs-validation')

      // Loop over them and prevent submission
      Array.prototype.slice.call(forms).forEach(function (form) {
        form.addEventListener(
          'submit',
          async function (event) {
            // TODO:檢查dist資料夾是否有doLogin.php
            event.preventDefault()
            if (!form.checkValidity()) {
              event.stopPropagation()
            } else {
              // TODO:測試階段可直接登入，實際開發需解開axios註解
              // 警告！！需在資料庫追加hacoin欄位，並於接口返回該使用者所持有的hacoin！！
              // let data = new FormData()
              // data.append('account', vm.account)
              // data.append('password', vm.password)
              // let { data: result } = await vm.$axios.post('./doLogin.php', data)
              // if (result.status == 1) {
              //   localStorage.setItem('token', JSON.stringify(result))
              //   vm.$emit('loginSuccess')
              //   vm.$router.push('/center')
              // } else {
              //   vm.$refs.accountChecker.classList.remove('d-none')
              // }
              let result = {
                account: vm.account,
                userName: '測試帳號',
                hacoin: 500
              }
              localStorage.setItem('token', JSON.stringify(result))
              vm.$emit('loginSuccess')
              vm.$router.push('/center')
            }

            form.classList.add('was-validated')
          },
          false
        )
      })
    })()
  }
}
</script>

<style></style>
